@import './variable.css';

.bk-slider {
    &.disable {
       .slide-inner {
            background: $newGreyColor;
       }
       .slider-button {
            border-color: $newGreyColor;
            cursor: default;
        }
    }
    .slide-bar {
        width: 100%;
        height: 4px;
        background: #F0F1F5;
        border-radius: 50px;
        position: relative;
    }
    .slide-inner {
        width: 0%;
        height: 100%;
        border-radius: 50px;
        background: $newMainColor;
        position: absolute;
        left: 0;
    }
    .slider-button {
        width: 12px;
        height: 12px;
        border: 2px solid $newMainColor;
        border-radius: 50%;
        background-color: #fff;
        position: absolute;
        left: 20%;
        top: 50%;
        margin: -6px 0 0 -6px;
        /* transition: all .2s linear; */
        cursor: grab;
        &:hover {
            transform: scale(1.3);
        }
        &:focus {
            box-shadow: 0px 0px 10px 0px rgba(58, 132, 255, 0.9);
        }
    }
